import React from "react";
import { Route, Switch} from "react-router-dom";
import Router from "./Router";
import AdminPage from "./pages/AdminsPages";

import store from "./redux/store";
import {Provider} from "react-redux";



const App = () => {
  return (
    <Switch>
        <Route path="/admin" component={AdminPage}/>
        <Route path="/Orders" component={AdminPage}/>
        <Route path="/Products" component={AdminPage}/>
        <Route path="/Categories" component={AdminPage}/>
        <Route path="/Services" component={AdminPage}/>
        <Route path="/Prices" component={AdminPage}/>
        <Route path="/AboutUs" component={AdminPage}/>
        <Route path="/login" component={AdminPage}/>
        <Provider store={store}>
            <Route component={Router}/>
        </Provider>
    </Switch>
  );
};


export default App;
